import React from 'react';
import { useGeolocation } from '../context/GeolocationContext';

function UserLocation() {
  const { position, heading, error } = useGeolocation();

  if (error) {
    return <div>–û—à–∏–±–∫–∞ –≥–µ–æ–ª–æ–∫–∞—Ü–∏–∏: {error}</div>;
  }

  if (!position) {
    return <div>–û–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏—è...</div>;
  }

  return (
    <div>
      <h2>–í–∞—à–µ –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ:</h2>
      <p>–®–∏—Ä–æ—Ç–∞: {position.latitude.toFixed(6)}</p>
      <p>–î–æ–ª–≥–æ—Ç–∞: {position.longitude.toFixed(6)}</p>
      {heading !== null && <p>–ù–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ: {heading.toFixed(2)}¬∞</p>}
    </div>
  );
}

export default UserLocation;